export default {
	props: ["bookingStauts", "UserInfoB"],
	name: "components",
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			avatar: '',
			nickname: '',
			userInfo: '',
		};
	},
	mounted() {		
		console.log(this.UserInfoB);
		// this.avatar = this.UserInfo.avatarUrl
		// this.nickname = this.UserInfo.nickName
		// this.GetUserInfo()
	},
	methods: {
		AboutUs(){
			uni.showToast({
				icon: 'none',
				title: '请等待版本更新'
			})
		},
		ContactService(){
			uni.showModal({
				title: "请发送邮件至srwswlkj_0793@163.com",
				confirmText: '复制邮箱',
				success(res) {
					if(res.confirm){
						uni.setClipboardData({
							data: "srwswlkj_0793@163.com"
						})
					}
				}
			})
			
		},
		UserTerms(){
			uni.navigateTo({
				url: '../user-service/user-service'
			})
		},
		GetUserInfo(){
			var that = this
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'user/info',
				success(res) {
					if(res.data.code == 0){
						that.userInfo = res.data.data
					} else if (res.data.code == 100) {
						uni.showToast({
							title: '登录超时，请重新授权登录',
							duration: 500
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 500)
					} else {
						uni.showModal({
							content: res.data.msg
						})
					}
				}
			})
		},
		goFollow(){
			uni.navigateTo({
				url: '../follows/follows'
			})
		},
		toChild(e) {
			uni.navigateTo({
				url: e.currentTarget.dataset.url
			})
		},
		goBooking(){
			uni.navigateTo({
				url: '../booking/booking'
			})
		},
		goBookingHis(){
			uni.navigateTo({
				url: '../booking-history/booking-history'
			})
		},
		goPresentHis(){

			uni.navigateTo({
				url: '../present-history/present-history'
			})
		},
	},
}
